import React from "react";
import "./Pagecomp.css"




function Pagecomp({name, url, images, tags}) {
  
  return (
    <div>
      <div className="card">
        <div className="card-header">
          <img src={images.default} alt="rover" />
        </div>
        <div className="card-body">
          <div className="slills">
            {
              tags.map((tag) => <span key={tag.name}>{tag.name}</span>)
            }
            
        
          </div><a href={url} target="_blank" rel="noopener noreferrer">

          <h4 className="head-text">{name}</h4>
          </a>
          {/* <p>An exploration into the truck's polarising design</p> */}
        </div>
      </div>
    </div>
  );
}

export default Pagecomp;
